//===================== Start Main Rulees ====================
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: cursive, sans-serif;
  outline: none;
  border: none;
  color: white;
}
a { text-decoration: none }
ul { list-style: none }
//===================== End Main Rulees ====================

//------------------------ Stat Creative Apple Landing Page ------------------------
.container {
  background: #000;
  min-height: 100vh;
  width: 100%;
  position: relative;
  //--- Header
  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    margin: auto;
    padding: 20px 0;
    //--- Logo
    .logo img {
      width: 50px;
    }
    //--- Navbar
    ul {
      display: flex;
      justify-content: space-between;
      align-items: center;
      li a {
        padding: 5px 15px;
        margin-right: 5px;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
  //--- Content
  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    margin: auto;
    overflow: hidden;
    //--- Text
    .text {
      width: 40%;
      h1 {
        font-size: 2.5rem;
        text-transform: uppercase;
      }
      p {
        font-size: 1.2rem;
        margin: 30px 0;
        line-height: 1.7;
      }
      a {
        background: white;
        color: black;
        font-size: 1.2rem;
        padding: 10px 25px;
        border-radius: 20px;
      }
    }
    //--- Image
    .image {
      width: 30%;
      img {
        width: 250px;
        object-fit: cover;
      }
    }
  }
  //--- Icons
  .icons {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    //--- images
    img {
      width: 50px;
      object-fit: cover;
      transition: .3s;
      &:hover {
        transform: scale(1.2);
        cursor: pointer;
      }
    }
  }
}
//------------------------ End Creative Apple Landing Page ------------------------